body {

    font-family: "DM Sans", sans-serif;
    font-optical-sizing: auto;
    /* font-weight: <weight>; */
    font-style: normal;
    color: black;

    /* background: linear-gradient(to right, #ff7e5f, #feb47b); */
}

.flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.inline-flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.title {
    font-size: 50pt;
    font-weight: 600;
}

.title-letter {
    opacity: 0;
    animation: fadeIn 0.5s ease forwards;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.coral-runner {
    background: linear-gradient(to right, #ff70709f, #ff6f6f8c);
    width: 70%;
    height: 200px;
    text-align: center;
    align-items: center;
}

.two-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.two-container-left {
    width: 40%;
    padding: 20px;
    font-size: 15pt;
    font-weight: 500
}

.two-container-right {
    width: 40%;
    padding: 20px;
    font-size: 15pt;
    font-weight: 500
}

.fading-text {
    background: linear-gradient(to right, black, black 70%, transparent);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    display: inline-block;
}

.fading-text-mask {
    mask-image: linear-gradient(to right, rgba(0,0,0,1), rgba(0,0,0,0.5), rgba(0,0,0,0));
    -webkit-mask-image: linear-gradient(to right, rgba(0,0,0,1), rgba(0,0,0,0.5), rgba(0,0,0,0));
}

.main-body-element {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.main-body-container {
    font-size: 17pt;
    width: 60%;
    text-align: left;
}