header {
    width: 100%;
    height: 80px;
    border-bottom: 1px solid black;
    background-color: rgba(255, 255, 255, 0.205);
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    font-size: 3vmin;
    align-items: center;
}

.header-left {
    width: 40%;
    font-size: 4vmin;
    font-weight: bold;
    letter-spacing: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-wrap: wrap;
}

.header-right {
    width: 60%;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-items: center;
}

.header-link {
    cursor: pointer;
    padding-left: 20px;
    padding-right: 20px;
    height: 80px;
    transition: background-color 0.2s ease;
}

.header-link p {
    margin-top: 20px;
}

.header-link:hover {
    background-color: rgba(200, 200, 200, 0.205);
}

a, a:visited {
    color: inherit;
    text-decoration: none;
}

.social-link {
    font-size: 1vmin;
    align-items: center;
    letter-spacing: normal;
}

.social-link:hover {
    background-color: rgba(200, 200, 200, 0.205);
}

.social-link img {
    height: 20px;
    width: 20px;
    margin-right: 5px;
    margin-left: 5px;
    position: relative;
    top: 4px;
}

.social-underline {
    position: relative;
    bottom: 0;
    left: 30px;
    height: 2px;
    background-color: black;
    width: 0px;
    transition: width 0.3s ease;
}
